import React from 'react';
import {
  Route,
  Switch,
  Redirect,
  useHistory,
} from 'react-router-dom';
import api from '../utils/api';
import Main from './Main';
import Login from './Login';
import Header from './Header';
import Footer from './Footer';
import Loader from './Loader/Loader';
import Register from './Register';
import ErrorPage from './Error/ErrorPage';
import ImagePopup from './ImagePopup';
import InfoTooltip from './InfoTooltip';
import AddPlacePopup from './AddPlacePopup';
import ErrorBoundary from './Error/ErrorBoundary';
import ProtectedRoute from './ProtectedRoute';
import EditAvatarPopup from './EditAvatarPopup';
import DeleteCardPopup from './DeleteCardPopup';
import EditProfilePopup from './EditProfilePopup';
import CurrentUserContext from '../context/CurrentUserContext';
import Navbar from './Navbar';
import Page from './Page';

function App() {
  const history = useHistory();
  const [isCard, setIsCard] = React.useState({});
  const [isAddPlacePopupOpen, setAddPlacePopup] = React.useState(false);
  const [isEditAvatarPopupOpen, setEditAvatarPopup] = React.useState(false);
  const [isEditProfilePopupOpen, setEditProfilePopup] = React.useState(false);
  const [isConfirmDeletePopupOpen, setConfirmDeletePopup] = React.useState(false);
  const [isNavbarOpen, setNavbarOpen] = React.useState(false);
  const [currentUser, setCurrentUser] = React.useState({
    name: '',
    about: '',
    _id: '',
    avatar: '',
    email: '',
  }); // —Ç—É—Ç –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ–±–æ –º–Ω–µ —Å —Å–µ—Ä–≤–µ—Ä–∞
  const [cards, setCards] = React.useState([]); // —Ç—É—Ç –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –∫–∞—Ä—Ç–æ—á–∫–∞—Ö
  const [loading, setLoading] = React.useState(false); // –ª–æ–∞–¥–µ—Ä –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
  const [statusOk, setIsOk] = React.useState(true); // —Ñ–ª–∞–≥ –¥–ª—è –æ—à–∏–±–∫–∏ —Å–µ—Ä–≤–µ—Ä–∞
  const [loggedIn, setLoggedIn] = React.useState(false);

  const [statusError, setError] = React.useState({}); // —Ñ–ª–∞–≥ –¥–ª—è –æ—à–∏–±–∫–∏ —Å–µ—Ä–≤–µ—Ä–∞
  const [isOpenCard, setOpenCard] = React.useState(false); // –∑–Ω–∞—á–µ–Ω–∏–µ –¥–ª—è –ø–æ–ø–∞–ø–∞ —Å –∫–∞—Ä—Ç–∏–Ω–∫–æ–π
  const [isOpenCheck, setOpenCheck] = React.useState(true); // –æ–∫–Ω–æ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
  const [isTooltip, setTooltip] = React.useState({
    isOpenTool: false,
    status: false,
    message: '',
  });
  const [selectedCard, setSelectedCard] = React.useState({}); // –æ–±—ä–µ–∫—Ç –¥–ª—è –ø–æ–ø–∞–ø–∞ —Å –∫–∞—Ä—Ç–∏–Ω–∫–æ–π
  const [buttonLoading, setButtonLoading] = React.useState(false); // –õ–æ–∞–¥–µ—Ä –¥–ª—è –∫–Ω–æ–ø–∫–∏ —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å.
  const [userAuthInfo, setUserAuthInfo] = React.useState({
    link: '/sign-up',
    info: '',
  });

  function infoMessage(text, bool) {
    setTooltip({
      ...isTooltip,
      isOpenTool: true,
      status: bool,
      message: text,
    });
  }

  function closeAllPopups() {
    // –∑–∞–∫—Ä—ã—Ç–∏–µ –≤—Å–µ—Ö –ø–æ–ø–∞–ø–æ–≤
    setEditAvatarPopup(false);
    setEditProfilePopup(false);
    setAddPlacePopup(false);
    setConfirmDeletePopup(false);
    setOpenCard(false);
    setButtonLoading(false);
    setOpenCheck(true);
    setTooltip({
      ...isTooltip,
      isOpenTool: false,
      message: '',
    });
  }

  function handleLogin(evt) {
    evt.preventDefault();
    history.push('/');
    setLoggedIn(true);
    setLoading(true);
  }

  function start(string) {
    api.token = localStorage.getItem(string);
    Promise.all([api.getInfoForUser(), api.getInfoForCards()])
      .then(([dataUser, dataCards]) => {
        setCurrentUser({ ...dataUser });
        setCards([...dataCards]);
        setUserAuthInfo({
          info: '',
          link: '/sign-up',
        });
        setIsOk(true);
      })
      .catch((err) => {
        console.error('–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è —Å–µ—Ä–≤–µ—Ä–∞ —Å –æ—à–∏–±–∫–æ–π', err.message);
        setError(err);
        setIsOk(false);
        localStorage.removeItem('jwt');
        setLoggedIn(false);
      })
      .finally(() => {
        setLoading(false);
      });
  }

  function onLogin(evt, emailAndPassword) {
    setButtonLoading(true);
    api
      .authorizationPost({
        ...emailAndPassword,
      })
      .then((data) => {
        setButtonLoading(false);
        if (data.token) {
          localStorage.setItem('jwt', data.token);
          handleLogin(evt);
          start('jwt');
          setOpenCheck(false);
          infoMessage('–î–æ–±—Ä–æ –ø–æ–∂–∞–ª–æ–≤–∞—Ç—å –Ω–∞ –ø—Ä–æ–µ–∫—Ç Mesto', true);
        } else if (!data.token && data.message) {
          infoMessage(data.message, false);
        }
      })
      .catch((err) => {
        infoMessage('', false);
        console.error(err);
      })
      .finally(() => {
        setButtonLoading(false);
      });
  }

  function onRegister(arg) {
    setButtonLoading(true);
    api
      .register(arg)
      .then((res) => {
        if (res.data) {
          setButtonLoading(false);
          infoMessage('', true);
          localStorage.setItem('email', res.data.email);
          history.push('/sign-in');
          setUserAuthInfo({ ...userAuthInfo, link: '/sign-up' });
        } else if (res.error) {
          infoMessage(res.error, false);
        } else if (res.message) {
          infoMessage(res.message, false);
        } else {
          console.error('–¥—Ä—É–≥–∞—è –æ—à–∏–±–∫–∞: res');
        }
      })
      .catch((err) => {
        infoMessage('', false);
        console.error(err);
      })
      .finally(() => {
        setButtonLoading(false);
      });
  }

  function handleLogOut(evt) {
    evt.preventDefault();
    setLoggedIn(false);
  }

  function signOut(evt) {
    if (evt.target.text === '–í—ã–π—Ç–∏') {
      if (localStorage.getItem('jwt')) {
        localStorage.removeItem('jwt');
        localStorage.removeItem('email');
        setCurrentUser({
          name: '',
          about: '',
          _id: '',
          avatar: '',
          email: '',
        });
        handleLogOut(evt);
        history.push('/sign-in');
      } else {
        localStorage.clear();
        handleLogOut(evt);
        history.push('/sign-in');
      }
    } else if (evt.target.text === '–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è') {
      history.push('/sign-up');
      setUserAuthInfo({ ...userAuthInfo, link: '/sign-in' });
    } else if (evt.target.text === '–í—Ö–æ–¥') {
      history.push('/sign-in');
      setUserAuthInfo({ ...userAuthInfo, link: '/sign-up' });
    }
  }

  function toggleNavbar(evt) {
    if (evt.target.checked) {
      setNavbarOpen(true);
    } else {
      setNavbarOpen(false);
    }
  }

  function handleUpdateUser({ name, about }) {
    // –ø–æ–ª—É—á–∞–µ–º –Ω–æ–≤—É—é –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è  —Å —Å–µ—Ä–≤–µ—Ä–∞
    setButtonLoading(true);

    api
      .updateUserInfo({ name, about })
      .then((infoUser) => {
        setCurrentUser({
          ...currentUser,
          name: infoUser.name,
          about: infoUser.about,
        });
      })
      .catch((err) => console.error('–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —Å –æ—à–∏–±–∫–æ–π', err))
      .finally(() => {
        setButtonLoading(false);
        closeAllPopups();
      });
  }

  function handleUpdateAvatar({ avatar }) {
    setButtonLoading(true);
    // –ø–æ–ª—É—á–∞–µ–º –æ–±–Ω–æ–≤–ª–µ–Ω–Ω—ã–π –∞–≤–∞—Ç–∞—Ä —Å —Å–µ—Ä–≤–µ—Ä–∞
    api
      .updateUserAvatar({ avatar })
      .then((infoAvatar) => {
        setCurrentUser({ ...currentUser, avatar: infoAvatar.avatar });
      })
      .catch((err) => console.error('–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —Å –æ—à–∏–±–∫–æ–π', err))
      .finally(() => {
        setButtonLoading(false);
        closeAllPopups();
      });
  }

  function handleAddPlace({ name, link }) {
    setButtonLoading(true);
    // –ø–æ–ª—É—á–∞–µ–º –Ω–æ–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É —Å —Å–µ—Ä–≤–µ—Ä–∞ –∏ –≤—Å—Ç–∞–≤–ª—è–µ–º –≤ –Ω–∞—á–∞–ª–æ
    api
      .addCard({ name, link })
      .then((newCard) => {
        if (!newCard) {
          return Promise.reject(new Error('–æ—à–∏–±–∫–∞ –¥–∞–Ω–Ω—ã—Ö'));
        }
        return setCards([newCard, ...cards]);
      })
      .catch((err) => console.error('–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏ —Å –æ—à–∏–±–∫–æ–π', err))
      .finally(() => {
        closeAllPopups();
      });
  }

  function handleEditAvatarClick() {
    setEditAvatarPopup(true);
  }
  function handleEditProfileClick() {
    setEditProfilePopup(true);
  }
  function handleAddPlaceClick() {
    setAddPlacePopup(true);
  }
  function handleConfirmDeleteClick(card) {
    setConfirmDeletePopup(true);
    setIsCard(card);
  }
  function handleCardClick({ ...props }) {
    // –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è –ø–æ–ø–∞–ø–∞ —Å –∫–∞—Ä—Ç–∏–Ω–∫–æ–π
    setSelectedCard({ ...props });
    setOpenCard(true);
  }

  function handleCardLike({ likes, _id }) {
    // –ø–æ–ª—É—á–∞–µ–º –ª–∞–π–∫–∏ —Å —Å–µ—Ä–≤–µ—Ä–∞
    const isLiked = likes.some((i) => i === currentUser._id);

    api
      .changeLikeCardStatus(_id, !isLiked)
      .then((newCard) => {
        const newCards = cards.map((c) => (c._id === _id ? newCard : c));
        setCards(newCards);
      })
      .catch((err) => console.error('–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –ø–æ –∫–∞—Ä—Ç–æ—á–∫–∞–º —Å –æ—à–∏–±–∫–æ–π', err.message));
  }

  function handleCardDelete({ _id }) {
    // —É–¥–∞–ª—è–µ–º –∫–∞—Ä—Ç–æ—á–∫—É
    const idCard = _id;
    setButtonLoading(true);
    api
      .deleteCard(_id)
      .then(() => {
        setCards(cards.filter((card) => card._id !== idCard));
      })
      .catch((err) => console.error('–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –ø–æ –∫–∞—Ä—Ç–æ—á–∫–∞–º —Å –æ—à–∏–±–∫–æ–π', err.message))
      .finally(() => {
        setButtonLoading(false);
        closeAllPopups();
      });
  }

  function closeAllPopupsEsc(evt) {
    if (evt.key === 'Escape') {
      closeAllPopups();
    }
  }

  async function toggleEventListenerWindow(bool) {
    if (bool) {
      window.addEventListener('keydown', closeAllPopupsEsc);
    } else {
      window.removeEventListener('keydown', closeAllPopupsEsc);
    }
  }

  React.useEffect(() => {
    if (localStorage.getItem('jwt')) {
      setLoading(true);
      setLoggedIn(true);
      start('jwt');
    } else {
      localStorage.clear();
    }
  }, []);

  return (
    <React.Fragment>
    {loggedIn
      ? <Redirect to='/' />
      : <Redirect to='/sign-in' />
    }
      <Page>
        <CurrentUserContext.Provider value={currentUser}>
          <ErrorBoundary>
          {isNavbarOpen && (
            <Navbar
              selectorPlace={'page'}
              link={userAuthInfo.link}
              signOut={signOut}
            />
          )}
            <Header
              selectorPlace={'header'}
              link={userAuthInfo.link}
              isNavbarOpen={isNavbarOpen}
              signOut={signOut}
              toggleNavbar={toggleNavbar}
            />
            <InfoTooltip
              isOpen={isTooltip}
              onClose={closeAllPopups}
              toggleEventListenerWindow={toggleEventListenerWindow}
            />
            <Switch>
              <ProtectedRoute exact path='/' loggedIn={loggedIn}>
              {loading && <Loader />}
                <React.Fragment>
                  <Main
                    cards={cards}
                    handleCardLike={handleCardLike}
                    onAddPlace={handleAddPlaceClick}
                    handleCardClick={handleCardClick}
                    onEditAvatar={handleEditAvatarClick}
                    onEditProfile={handleEditProfileClick}
                    handleCardDelete={handleConfirmDeleteClick}
                  />
                  <AddPlacePopup
                    isOpen={isAddPlacePopupOpen}
                    isLoadingButton={buttonLoading}
                    onClose={closeAllPopups}
                    onAddPlace={handleAddPlace}
                    toggleEventListenerWindow={toggleEventListenerWindow}
                  />
                  <EditAvatarPopup
                    isOpen={isEditAvatarPopupOpen}
                    isLoadingButton={buttonLoading}
                    onClose={closeAllPopups}
                    onUpdateAvatar={handleUpdateAvatar}
                    toggleEventListenerWindow={toggleEventListenerWindow}
                  />
                  <EditProfilePopup
                    isOpen={isEditProfilePopupOpen}
                    isLoadingButton={buttonLoading}
                    onClose={closeAllPopups}
                    onUpdateUser={handleUpdateUser}
                    toggleEventListenerWindow={toggleEventListenerWindow}
                  />
                  <DeleteCardPopup
                    isCard={isCard}
                    isLoadingButton={buttonLoading}
                    isOpen={isConfirmDeletePopupOpen}
                    onClose={closeAllPopups}
                    onDeleteCard={handleCardDelete}
                    toggleEventListenerWindow={toggleEventListenerWindow}
                  />
                  <ImagePopup
                    isOpen={isOpenCard}
                    selectedCard={selectedCard}
                    onClose={closeAllPopups}
                    toggleEventListenerWindow={toggleEventListenerWindow}
                  />
                </React.Fragment>
              {!statusOk && <ErrorPage error={statusError} />}
              </ProtectedRoute>
              {!loading && (
                <Route path='/sign-in' exact>
                  <Login
                    isOpen={isOpenCheck}
                    isLoadingButton={buttonLoading}
                    onLogin={onLogin}
                    handleLogin={handleLogin}
                  />
                </Route>
              )}
              <Route path='/sign-up' exact>
                <Register
                  isOpen={isOpenCheck}
                  isLoadingButton={buttonLoading}
                  signOut={signOut}
                  onRegister={onRegister}
                />
              </Route>
            </Switch>
            {loggedIn && <Footer />}
          </ErrorBoundary>
        </CurrentUserContext.Provider>
      </Page>
    </React.Fragment>
  );
}

export default App;
